
#app {
  font-family: "Roboto Regular", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f2f2f2;
}
.w100 {
  width: 100%;
}
.title {
  font-family: Roboto-Medium;
  font-size: 24px;
  color: #000000;
}
.title2 {
  font-family: Roboto-Medium;
  font-size: 36px;
  color: #000000;
}
.subTitle {
  font-family: Roboto-Medium;
  font-size: 20px;
  color: #000000;
}
.remark {
