      flex-basis: 250px;
  background: white;
}
.activityItem__media > img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.activityItem__content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 40px 30px;
  background-color: white;
}
.activityItem__name {
